####  Mixin

 Mixin 

1. **** <br>
  - lt* : less than
  - gt* : greater than
  - in* : in range
1. **** <br>
   **** 

```scss
// e.g.
@mixin flex-ltXS {...}
.u-visible-gtSM {...}
```

>  `in*` 
>
> ```scss
> // e.g.
> // .u-hidden-inXXS {...} =>
> .u-hidden-xxs {...}
> ```

