@font-face {
  font-family: "glyphs";
  src: url('../fonts/glyph/glyphs.eot');
  src: url('../fonts/glyph/glyphs.eot?#iefix') format('eot'),
    url('../fonts/glyph/glyphs.woff2') format('woff2'),
    url('../fonts/glyph/glyphs.woff') format('woff'),
    url('../fonts/glyph/glyphs.ttf') format('truetype'),
    url('../fonts/glyph/glyphs.svg#glyphs') format('svg');
  font-weight: normal;
  font-style: normal;
}

.glyph {
  font-family: "glyphs";
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  vertical-align: middle;
}

// ----------------------------------------
// Icon Sizes
// ----------------------------------------

// makes the font 33% larger relative to the icon container

.glyph-lg {
  font-size: 1.1em;
  line-height: (3em / 4);
  vertical-align: -.0667em;
}

.glyph-xs {
  font-size: .75em;
}

.glyph-sm {
  font-size: .875em;
}

@for $i from 1 through 10 {
  .glyph-#{$i}x {
    font-size: $i * 1em;
  }
}

// ----------------------------------------
// Fixed Width Icons
// ----------------------------------------
.glyph-fw {
  text-align: center;
  width: (20em / 16);
}

// ----------------------------------------
// Animated Icons
// ----------------------------------------

.glyph-spin {
  animation: fa-spin 2s infinite linear;
}

.glyph-pulse {
  animation: fa-spin 1s infinite steps(8);
}

@for $i from 2 through 20 {
  .glyph-pulse-#{$i} {
    animation: fa-spin 1s infinite steps(#{$i});
  }
}
@for $i from 1 through 20 {
  .glyph-spin-#{$i} {
    animation: fa-spin #{8/$i}s infinite linear;
  }
}
@for $i from 1 through 20 {
  .glyph-speed-#{$i} {
    animation-duration: #{8/$i}s;
  }
}

@keyframes fa-spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}



